/**
 * @module model-bus-contact-approvals-ng
 *
 * @description
 * Contact Approvals widget model.
 *
 * @example
 * import modelContactApprovalsModuleKey,
 *  { modelContactApprovalsKey } from 'model-bus-contact-approvals-ng';
 */
import angular from 'vendor-bb-angular';

import contactDataModuleKey, { contactDataKey } from 'data-bb-contact-http-ng';

import contactApprovalsModel from './contact-approvals';

export const moduleKey = 'model-bus-contact-approvals-ng';
export const modelContactApprovalsKey = '${moduleKey}:model';

export default angular
  .module(moduleKey, [contactDataModuleKey])

  .factory(modelContactApprovalsKey, [
    contactDataKey,
    /* into */
    contactApprovalsModel,
  ])

  .name;



